//import './style.css';

const createTask = document.querySelector("#createTask");
const addTask = document.querySelector("#addTask");
const tasks = document.querySelector(".tasks");
const emptyTodo = document.querySelector(".emptyTodo")

const displayTodo = ()=> {
    addTask.addEventListener("click", (Event)=>{
        Event.preventDefault()
        if (createTask.value === "") {
            console.log("champ vide");
        }
        if (createTask.value != "") {
            console.log(createTask.value);
            emptyTodo.remove()
            //createTask.value = "";
        }

        const taskList = document.createElement("div");
        const taskName = document.createElement("span");
        const deleteTask = document.createElement("button");
        const editTask = document.createElement("button");
        const done = document.createElement("input")

        editTask.textContent = "Edit"
        deleteTask.textContent = "Delete";
        taskName.textContent = createTask.value;
        done.type = "checkbox"
        tasks.append(taskList);
        taskList.append(done, taskName, editTask,deleteTask);
        taskList.setAttribute("class", "taskList")
        createTask.value = "";
    })
}

displayTodo()